import React, {Component} from 'react';
import {message,Row, Col,Icon, Timeline} from 'antd';
import Markdown  from 'react-markdown';




class About extends Component {
    constructor(props) {
        super(props);
        if (window.erred && window.erred > 3 && window.erred < 6) {
            message.error('你已经受限访问');
            window.erred = window.erred + 1;
        } else if (window.erred && window.erred > 5) {
            window.location.replace("http://news.baidu.com/")
        }

        var oneDay = 24*60*60*1000;
        var start = new Date();
        this.state = {
            oneDay:oneDay ,
            start :start,

        }
    }





    render() {

        const {start,oneDay } = this.state;

        return (
            <div className="gutter-example">
                <Row gutter={8}>
                    <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                        <p>邮 箱 <Icon type="mail" style={{marginRight: 8}}/>nick070809@163.com</p>
                        <p>坐 标 <Icon type="environment-o" style={{marginRight: 8}}/>杭州·中国</p>
                        <p>能 力 <Icon type="dashboard" style={{marginRight: 8}}/>java /python /scala /mysql /oracle
                            /react</p>
                        <p>最 近 <Icon type="instagram" style={{marginRight: 8}}/>c++</p>
                        <p>&nbsp;</p>
                    </Col>

                    <Col xs={24} sm={24} md={3} lg={3} xl={3}></Col>


                    <Col xs={24} sm={24} md={9} lg={9} xl={9}>

                        <Timeline >
                            <Timeline.Item color="blue">2016-杭州</Timeline.Item>
                            <Timeline.Item color="green">2012-重庆</Timeline.Item>
                            <Timeline.Item color="green">2010-福州</Timeline.Item>
                            <Timeline.Item color="red">2006-杭州</Timeline.Item>
                        </Timeline>
                    </Col>
                </Row>

                <p> 距离2019 双11  还有 { Math.ceil((new Date(2019,(11-1),11) -start)/oneDay) } 天 , 距离2020 春节  还有 { Math.ceil((new Date(2020,(1-1),25) -start)/oneDay) } 天</p>
                <p> 距离2020 双11  还有 { Math.ceil((new Date(2020,(11-1),11) -start)/oneDay) } 天 , 距离2021 春节  还有 { Math.ceil((new Date(2021,(2-1),11) -start)/oneDay) } 天</p>
                <p> 距离2021 双11  还有 { Math.ceil((new Date(2021,(11-1),11) -start)/oneDay) } 天 , 距离2022 春节  还有 { Math.ceil((new Date(2022,(2-1),1) -start)/oneDay) } 天</p>
                <p> 距离2022 亚运  还有 { Math.ceil((new Date(2022,(9-1),10) -start)/oneDay) } 天 , 距离2022 双11  还有 { Math.ceil((new Date(2022,(11-1),11) -start)/oneDay) } 天</p>

                <p> 距离2019 10月18日  还有 { Math.ceil((new Date(2019,(10-1),18) -start)/oneDay) }天</p>


            </div>
        );
    }
}

export default About;


